.intro{
    display:flex;
    height:77vh;
    margin-top:6rem;

}


.i-left{
    display:flex;
    position: relative;
    flex:1;
    flex-direction: column;
    gap:2rem;
}

.i-right{
    flex:1;
}
.i-name>:nth-child(1){
    color:var(--black);
    font-weight: bold;
    font-size: 3rem;
    
}
.i-name>:nth-child(2){
    color:var(--orange);
    font-weight:bold;
    font-size: 3rem;
    
}
.i-name>:nth-child(3){
    color:var(--gray);
    font-weight:100;
    font-size: 14px;
    margin-top: 10px;
    
}
.i-button{
    width:6rem;
    height:2rem;
}

.i-right{
    flex:1;
    position: relative;

}
.i-right>*{
    position:absolute;
    z-index: 1;
    
}